export default{
  data(){
    return{
      a_semih : [], // 
      start : -1 , // 
      end : -1 , // 
      others:[],  // 
      myArr:[], // 
      b_shade_box:false, // 
      ok_myArr_service:[], // 
    }
  },
  props: {
    sevOtherArr : {
          type: Array,
          default () {
            return  [16,20,21,24,25,26]  // 
          }
        }
  },
  filters: {
    toSemih(arr, a_semih){
      if(!arr.length){
        return ''
      }
      // return a_semih[arr[0]-1]+" - "+a_semih[arr[arr.length-1]] // 8:00 - 8:30
      // return a_semih[arr[0]]+" - "+a_semih[arr[arr.length-1]] // 17 8:30 - 8:30

      // return a_semih[arr[0]]+" - "+a_semih[arr[arr.length-1]+1] // 8:30 - 9:00



      // 
      //  8:00  8:30  [16,17] 
      return a_semih[arr[0]]+" - "+a_semih[arr[arr.length-1]] // 17 8:30 - 8:30
    }
  },
  created(){
    // others
      var arr1=this.sevOtherArr;
      this.others = arr2arr(arr1)

    // a_semih
      var timeBlock=[];
      for(var i=0;i<=23;i++){
        timeBlock.push({
          index:i, //  24
          semih:[i*2, i*2+1], // 00:00-0:30  48
          class:['left', 'right'], // class  index  left
        })
        timeBlock[i].info=null;
        timeBlock[i].txt=[];
        timeBlock[i].txt.push(i+':00');
        timeBlock[i].txt.push(i+':30');
      }

      var a_semih=[];
      for(var i=0; i<=timeBlock.length-1; i++){
        a_semih.push(timeBlock[i].txt[0])
        a_semih.push(timeBlock[i].txt[1])
      }
      this.a_semih = a_semih


  },
  methods:{
    confirm(){ // 
      this.b_shade_box=!this.b_shade_box;
      this.ok_myArr_service = this.myArr_service;
      this.$emit('myArr_service', this.ok_myArr_service)
    },
    cancel(){ // 
      this.b_shade_box=!this.b_shade_box;
      this.myArr=arr2arr(this.ok_myArr_service)

    },
    select_fn(index){

      var others = this.others
      for(var i=0;i<=others.length;i++){ // 
        if(index == others[i]){
          console.log('')
          return
        }
      }


      if( this.start < 0 && this.end < 0 ){ // 
        // 
          this.start=index // 
          this.end=-1
          this.myArr=[]
          this.$set(this.myArr,index,index)

      }else if( this.start >= 0 && this.end < 0){ // 
        console.log('', this.start)

        // 
          for(var i=0;i<=others.length;i++){
            if( (others[i] > this.start && others[i] < index) || (others[i] < this.start && others[i] > index)){ // 
              console.log('')
              // 
                this.start=index // 
                this.end=-1
                this.myArr=[]
                this.$set(this.myArr,index,index)

              return
            }
          }

        // 
          this.$set(this.myArr,index,index)
          this.end=index

        // 
          var abs = [this.start, this.end];
          abs.sort(function(a, b){return a - b});
          for(var i=abs[0]; i<=abs[1]; i++){
            this.$set(this.myArr,i,i)
          }

      }else if( this.start >=0 && this.end >= 0){ // 
        console.log('')
        this.start=index // 
        this.end=-1
        this.myArr=[]
        this.$set(this.myArr,index,index)
      }




    }
  },
  computed: {
    myArr_service(){
      var arr=this.myArr;
      var arr2=[];
      for(var i=0; i<arr.length; i++){
        if(arr[i]>=0){
          arr2.push(arr[i])
        }
      }

      return arr2
    }
  }

}


  function arr2arr(arr){
    for(var i=0;i<=arr.length;i++){
      arr[arr[i]] = arr[i]
    }
    return arr
  }





